import nodemailer from "nodemailer";

export default class Email {

	constructor(){
		this.nodemailer = nodemailer;

		this._anexos = [];
		this._destinatario = null;
		this._titulo = null;
		this._remetente = null;
	}

	anexo(filename){
		this._anexos.push({
			filename: filename.split("/").reverse()[0],
			path: filename,
			cid: String(Math.random())
		});
	}

	corpo(corpo){
		this._corpo = corpo;
	}

	destinatario(destinatario){
		if(Array.isArray(destinatario)){
			destinatario = destinatario.join(", ");
		}
		this._destinatario = destinatario;
	}

	enviar(success, fail){
		/*
		let transport = this.nodemailer.createTransport({
			host: "smtplw.com.br",
			port: 587,
			secure: false,
			auth: {
				user: "controlwareemail",
				pass: "alphadata1987"
			},
			tls: {
				rejectUnauthorized: false
			}
		});
		*/

		let transport = this.nodemailer.createTransport({
			host: "smtp-relay.sendinblue.com",
			port: 587,
			secure: false,
			auth: {
				user: "murilo@controlware.com.br",
				pass: "2xhH64OnTwBPYvKF"
			},
			tls: {
				rejectUnauthorized: false
			}
		});

		let message = {
			from: '"'+this._remetente+'" <satflex@controlware.com.br>',
			to: this._destinatario,
			subject: this._titulo,
			html: this._corpo,
			attachments: this._anexos
		};

		transport.sendMail(message, (err, info) => {
			if(err){
				fail(err);
				return err;
			}
			success();
			return true;
		});
	}

	remetente(remetente){
		this._remetente = remetente;
	}

	titulo(titulo){
		this._titulo = titulo;
	}

}
